<template>
  <calendar-maker :submit-api="submitUrl"
                  :model="model"
                  @save="save"></calendar-maker>
</template>

<script>
import CalendarMaker from './components/app'
import UrlHelper from 'utilities/url'

export default {
  name: 'app',
  components: {
    CalendarMaker
  },
  props: {
    fetchApi: {
      type: String,
      default: '/api/placeholders'
    },
    submitApi: {
      type: String,
      default: '/api/combine'
    }
  },
  data() {
    return {
      model: {}
    }
  },
  created() {
    let vm = this
    $.getJSON(UrlHelper.wrapUrl(vm.fetchApi)).then(res => {
      vm.model = res
    })
  },
  computed: {
    submitUrl() {
      return UrlHelper.wrapUrl(this.submitApi)
    }
  },
  methods: {
    save(res) {
      if (res.success && res.url) {
        // urlURL
        let link = document.createElement('a')
        link.href = res.url
        link.target = '_blank'
        link.click()
      } else if (!res.success && res.error) {
        // error
        alert(res.error)
      } else {
        console.log(['saved', res])
      }
    }
  }
}
</script>



